import React, { useEffect, Fragment } from 'react';
import Navbar from '../Components/Navbar';
import { connect } from 'react-redux';
import { checkAuthenticated } from '../Actions/Auth';
import { loadUser } from '../Actions/Profile';
import { getUsers } from '../Actions/Profile';

function Layout({ children, checkAuthenticated, loadUser, getUsers }) {
    useEffect(() => {
        checkAuthenticated();
        loadUser();
        getUsers();
    });

    return (
        <Fragment>
            <Navbar />
            {children}
        </Fragment>
    );
}

export default connect(null, { checkAuthenticated, loadUser, getUsers })(Layout);