import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { Autocomplete, Box, Link, TextField } from '@mui/material';

function SearchUsers( {users_global} ) {
    return (
        <Box>
            <Autocomplete
                autoComplete
                options={users_global}
                getOptionLabel={(option) => option.username}
                renderInput={(params) => <TextField {...params} label="Search Users..." />}
                renderOption={(props, option) => (
                    <Link href={`/profile/${option.username}`} underline='none' color='inherit'>
                        <Box {...props} component='li'>
                            {option.username}
                        </Box>
                    </Link>
                )}
                sx={{ width: 300 }}
            />
        </Box>
    );
}

const mapStateToProps = state => ({
    users_global: state.Profile.users
});

export default connect(mapStateToProps)(SearchUsers);