import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { loadOtherUser, follow, unfollow } from '../Actions/Profile';
import { Typography, Grid, Link, Button, Box, Paper } from '@mui/material'
import RatingCalendar from '../Components/RatingCalendar';
import RatingDisplay from '../Components/RatingDisplay';
import Divider from '@mui/material/Divider';

function Profile({
    loadOtherUser,
    follow,
    unfollow,
    first_name_global,
    last_name_global,
    bio_global,
    followers_global,
    following_global,
    ratings_global,
    username_local,
    following_local
}) {
    const {username} = useParams();
    const isLocalAccount = username_local===username;
    const [isFollowing, setIsFollowing] = useState(false);

    useEffect(() => {
        loadOtherUser(username);
        if (!isLocalAccount) {
            setIsFollowing(checkFollowing(username));
        }
    },[following_global.length, followers_global.length]);

    const averageDay = (ratings) => {
        let sum = 0;
        ratings.forEach(item => {
            sum += item['rating'];
        });
        let avg = sum / ratings.length
        return avg.toFixed(1);
    };

    const checkFollowing = (username) => {
        for (let i = 0; i < following_local.length; i+=1) {
            if (following_local[i]['username']===username) {
                return true;
            }
        }
        return false;
    };

    return (
        <Grid container spacing={4} sx={{ p: 10 }}>
                <Grid item xs={12}>
                    <Typography variant='h1' component='h3' display='inline'>{username} </Typography>    
                    <Link href={`/followers/${username}`} underline='hover' color='inherit' display='inline' variant='h5'>
                        {followers_global.length} followers
                    </Link> | <Link href={`/following/${username}`} underline='hover' color='inherit' display='inline' variant='h5'>
                        {following_global.length} following
                    </Link>
                </Grid>
                {!isLocalAccount ?
                    <Grid item xs={12}>
                        { isFollowing ?
                        <Button variant='outlined' onClick={() => unfollow(username)}>Unfollow</Button>
                        :
                        <Button variant='contained' onClick={() => follow(username)}>Follow</Button>
                        }
                    </Grid>
                    :
                    <div></div>
                }
                <Divider />
                <Grid item xs={4} sx={{ mb: 6 }}>
                    <Paper sx={{ width: 275, height: 225, display: 'flex', flexDirection: 'column', justifyContent: 'space-around', alignItems: 'center' }}>
                        <Typography variant='body' component='p'>Average:</Typography>
                        <RatingDisplay rating={averageDay(ratings_global)} />
                        <Typography variant='body' component='p'>Out of ten</Typography>
                    </Paper>
                </Grid>
                <Grid item xs={8} sx={{ mt: 1, mr: -1, display: 'flex', flexDirection: 'column' }}>
                    <Box>
                        <Typography variant='h4' component='p' display='inline'>{first_name_global} </Typography>
                        <Typography variant='h4' component='p' display='inline'>{last_name_global}</Typography>
                    </Box>
                    <Box sx={{ overflow: "hidden", textOverflow: "ellipsis", width: 800, wordWrap: 'break-word' }}>
                        <Typography variant='subtitle1'>{bio_global}</Typography>
                    </Box>
                </Grid>
                <Box sx={{ ml: 1 }}>
                    <RatingCalendar />
                </Box>
        </Grid>
    );
}

const mapStateToProps = state => ({
    first_name_global: state.Profile.other_first_name,
    last_name_global: state.Profile.other_last_name,
    bio_global: state.Profile.other_bio,
    followers_global: state.Profile.other_followers,
    following_global: state.Profile.other_following,
    ratings_global: state.Profile.other_ratings,
    username_local: state.Profile.username,
    following_local: state.Profile.following
});

export default connect(mapStateToProps, { loadOtherUser, follow, unfollow })(Profile);