import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { getRating, comment, like } from '../Actions/Profile';
import { Typography, Grid, Link, TextField, Box, IconButton, Button, Card } from '@mui/material'
import { createTheme, ThemeProvider } from '@mui/material/styles';
import RatingDisplay from '../Components/RatingDisplay';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';

function Profile({
    username,
    getRating,
    rating_global,
    other_username,
    comment,
    like
}) {
    const {id} = useParams();
    const [haveLiked, setHaveLiked] = useState(false);
    const [commented, setCommented] = useState(false);
    const [content, setContent] = useState('');

    useEffect(() => {
        getRating(id);
        setCommented(false);
    }, [haveLiked, commented]);

    useEffect(() => {
        checkIfLiked();
   });

    const checkIfLiked = () => {
        if (rating_global.likes) {
            for (let i = 0; i < rating_global.likes.length; i+=1) {
                if (rating_global.likes[i].username===username) {
                    setHaveLiked(true);
                }
            }
        }
    };

    const onChange = e => {
        setContent(e.target.value);
    };

    const onSubmit = e => {
        e.preventDefault();

        if (content.length > 10) {
            comment(id, content);
            setCommented(true);
        }
    };

    const theme = createTheme({
        typography: {
            fontFamily: '"Sono", "Helvetica", "Arial", sans-serif',
            h1: {
                fontSize: 250,
            },
        },
        palette: {
            mode: 'dark',
            primary: {
              main: '#ff5349',
            },
            secondary: {
              main: '#800020',
            },
          },
      });

    return (
        <Grid container spacing={4} sx={{ p: 10 }}>
            <Grid item xs={12}>
                <Box sx={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
                    <Link href={`/profile/${other_username}`} underline='hover'>
                        <Typography variant='h2'>{other_username}</Typography>
                    </Link>
                    {rating_global.likes ? 
                        haveLiked ? 
                            <Typography variant='subtitle1' color='primary' sx={{ ml: 2, mt: 1 }}>{rating_global.likes.length} <FavoriteIcon /></Typography>
                        :
                            <Typography variant='subtitle1' color='primary'>{rating_global.likes.length} <IconButton onClick={() => {
                                like(id);
                                setHaveLiked(true);
                            }} color='primary'><FavoriteBorderIcon /></IconButton></Typography>
                    : 
                        <Typography variant='subtitle1'>0 <FavoriteBorderIcon /></Typography>
                    }
                </Box>
            </Grid>
            <Grid item xs={6}>
                {/* <Typography variant='h3'>Rating</Typography> */}
                <ThemeProvider theme={theme}>
                    <Box sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                        <Typography variant='h1'>
                            <RatingDisplay rating={rating_global.rating} />
                        </Typography>
                    </Box>
                    <Card elevation={3} sx={{ overflow: "hidden", textOverflow: "ellipsis", wordWrap: 'break-word', p: 2 }}>
                        <Typography variant='h5'>
                            {rating_global.description}
                        </Typography>
                    </Card>
                </ThemeProvider>
            </Grid>
            <Grid item xs={6}>
                {/* <Typography variant='h3'>Comments</Typography> */}
                <Box sx={{ width: 600, mt: 8 }}>
                    <form onSubmit={e => onSubmit(e)}>
                        <TextField
                            id="content"
                            label="Make a comment..."
                            multiline
                            fullWidth
                            rows={2}
                            onChange={e => onChange(e)}
                        />
                        <Button
                            type="submit" 
                            color="primary" 
                            variant="contained"
                            sx={{ mt: 1 }}>
                            Comment!
                        </Button>
                    </form>
                </Box>
                <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
                    {rating_global.comments ? 
                        rating_global.comments.length > 0 ? 
                            rating_global.comments.map((comment) => 
                                <Card elevation={3} sx={{ height: 100, width: 400, p: 2, my: 1, display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                                    <Box>
                                        <Link href={`/profile/${comment.user.username}`} underline='hover' display='inline'>{comment.user.username}</Link>
                                        <Typography variant='caption' display='inline'> {comment.date}</Typography>
                                    </Box>
                                    <Typography variant='body1'>{comment.text}</Typography>
                                </Card>)
                        :
                            <Box sx={{ m: 1 }}>
                                <Typography variant='subtitle1'>No Comments</Typography>
                            </Box>
                    :
                    <Box sx={{ m: 1 }}>
                        <Typography variant='subtitle1'>No Comments</Typography>
                    </Box>
                    }
                </Box>
                
            </Grid>
            {/* maybe something like 'go back to home' button/link */}
        </Grid>
    );
}

const mapStateToProps = state => ({
    username: state.Profile.username,
    other_username: state.Profile.other_username,
    rating_global: state.Profile.rating,
    comments: state.Profile.comments,
    likes: state.Profile.likes
});

export default connect(mapStateToProps, { getRating, comment, like })(Profile);