import axios from 'axios';
import Cookies from 'js-cookie';
import {
    LOAD_USER_PROFILE_SUCCESS,
    LOAD_USER_PROFILE_FAIL,
    UPDATE_USER_PROFILE_SUCCESS,
    UPDATE_USER_PROFILE_FAIL,
    LOAD_OTHER_USER_PROFILE_SUCCESS,
    LOAD_OTHER_USER_PROFILE_FAIL,
    POST_RATING_SUCCESS,
    POST_RATING_FAIL,
    FOLLOW_SUCCESS,
    FOLLOW_FAIL,
    UNFOLLOW_SUCCESS,
    UNFOLLOW_FAIL,
    LOAD_USERS_SUCCESS,
    LOAD_USERS_FAIL,
    GET_RATING_SUCCESS,
    GET_RATING_FAIL,
    GET_RATINGS_SUCCESS,
    GET_RATINGS_FAIL,
    POST_LIKE_SUCCESS,
    POST_LIKE_FAIL,
    POST_COMMENT_SUCCESS,
    POST_COMMENT_FAIL
} from './Types';

export const loadUser = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        }
    };

    try {
        const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/profile`, config);

        dispatch({
            type: LOAD_USER_PROFILE_SUCCESS,
            payload: response.data
        });
    } catch (err) {
        console.log(err);
        dispatch({
            type: LOAD_USER_PROFILE_FAIL
        });
    }
};

export const loadOtherUser = (username) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        }
    };

    const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/other-profile?username=${username}`, config);
    if (response.data.profile && response.data.username) {
        dispatch({
            type: LOAD_OTHER_USER_PROFILE_SUCCESS,
            payload: response.data
        });
    } else {
        dispatch({
            type: LOAD_OTHER_USER_PROFILE_FAIL
        });
    }

};

export const updateProfile = (first_name, last_name, bio) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFTOKEN': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({
        'withCredentials': true,
        first_name,
        last_name,
        bio,
    });

    const response = await axios.patch(`${process.env.REACT_APP_API_URL}/api/update`, body, config);
    if (response.data.profile && response.data.username) {
        dispatch({
            type: UPDATE_USER_PROFILE_SUCCESS,
            payload: response.data
        });
    } else {
        dispatch({
            type: UPDATE_USER_PROFILE_FAIL,
        }); 
    }
};

export const rate = (rating, description) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({
        'withCredentials': true,
        rating,
        description
    });

    const response = await axios.post(`${process.env.REACT_APP_API_URL}/api/rate`, body, config);
    if (response.data.Success) {
        dispatch({
            type: POST_RATING_SUCCESS
        });
    } else {
        dispatch({
            type: POST_RATING_FAIL
        });
    }
};

