import {
    LOAD_USER_PROFILE_SUCCESS,
    LOAD_USER_PROFILE_FAIL,
    UPDATE_USER_PROFILE_SUCCESS,
    UPDATE_USER_PROFILE_FAIL,
    LOAD_OTHER_USER_PROFILE_SUCCESS,
    LOAD_OTHER_USER_PROFILE_FAIL,
    POST_RATING_SUCCESS,
    POST_RATING_FAIL,
    FOLLOW_SUCCESS,
    FOLLOW_FAIL,
    UNFOLLOW_SUCCESS,
    UNFOLLOW_FAIL,
    LOAD_USERS_SUCCESS,
    LOAD_USERS_FAIL,
    GET_RATING_SUCCESS,
    GET_RATING_FAIL,
    GET_RATINGS_SUCCESS,
    GET_RATINGS_FAIL,
    POST_LIKE_SUCCESS,
    POST_LIKE_FAIL,
    POST_COMMENT_SUCCESS,
    POST_COMMENT_FAIL
} from '../Actions/Types';

const initialState = {
    username: '',
    first_name: '',
    last_name: '',
    bio: '',
    followers: [],
    following: [],
    ratings: [],
    rating: [],
    other_username: '',
    other_first_name: '',
    other_last_name: '',
    other_bio: '',
    other_followers: [],
    other_following: [],
    other_ratings: [],
    users: []
};

export default function(state = initialState, action) {
    const { type, payload } = action;

    switch(type) {
        case LOAD_USER_PROFILE_SUCCESS:
            return {
                ...state,
                username: payload.username,
                first_name: payload.profile.first_name,
                last_name: payload.profile.last_name,
                bio: payload.profile.bio,
                followers: payload.profile.followers,
                following: payload.profile.following,
                ratings: payload.profile.ratings
            }
        case LOAD_OTHER_USER_PROFILE_SUCCESS:
            return {
                ...state,
                other_username: payload.username,
                other_first_name: payload.profile.first_name,
                other_last_name: payload.profile.last_name,
                other_bio: payload.profile.bio,
                other_likes: payload.profile.likes, 
                other_followers: payload.profile.followers,
                other_following: payload.profile.following,
                other_ratings: payload.profile.ratings
            }
        case UPDATE_USER_PROFILE_SUCCESS:
            return {
                ...state,
                username: payload.username,
                first_name: payload.profile.first_name,
                last_name: payload.profile.last_name,
                bio: payload.profile.bio,
            }
        case LOAD_USER_PROFILE_FAIL:
            return {
                ...state,
                username: '',
                first_name: '',
                last_name: '',
                bio: '',
            }
        case LOAD_OTHER_USER_PROFILE_FAIL:
            return {
                ...state,
                other_username: '',
                other_first_name: '',
                other_last_name: '',
                other_bio: '',
            }
        case FOLLOW_SUCCESS:
        case UNFOLLOW_SUCCESS:
            return {
                ...state,
                followers: payload.profile.followers,
                following: payload.profile.following,
                other_followers: payload.otherProfile.followers,
                other_following: payload.otherProfile.following
            }
        case LOAD_USERS_SUCCESS:
            return {
                ...state,
                users: payload.users
            }
        case GET_RATING_SUCCESS:
            return {
                ...state,
                rating: payload.rating,
                other_username: payload.username,
            }
        case GET_RATINGS_SUCCESS:
            return {
                ...state,
                other_ratings: payload.ratings
            }
        case POST_LIKE_SUCCESS:
        case POST_COMMENT_SUCCESS:
        case POST_COMMENT_FAIL:
        case POST_LIKE_FAIL:
        case GET_RATINGS_FAIL:
        case GET_RATING_FAIL:
        case LOAD_USERS_FAIL:
        case POST_RATING_SUCCESS:
        case POST_RATING_FAIL:
        case FOLLOW_FAIL:
        case UNFOLLOW_FAIL:
        case UPDATE_USER_PROFILE_FAIL:
            return {
                ...state
            }
        default:
            return state

    }
};