import React, { useEffect, useState } from 'react';
import { StaticDatePicker } from '@mui/x-date-pickers/StaticDatePicker';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { TextField, Typography, Box } from '@mui/material';
import dayjs from 'dayjs';
import { connect } from 'react-redux';

function RatingCalendar({ ratings_global }) {
    let currentDate = new Date().toJSON().slice(0, 10);
    const [date, setDate] = useState(currentDate);
    const [rating, setRating] = useState(() => {
        for (let i = 0; i < ratings_global.length; i+=1) {
            if (ratings_global[i].date===currentDate) {
                return `${ratings_global[i].rating} - ${ratings_global[i].description}`;
            }
        }
    });

    const getDayRating = (day, month, year) => {
        if (month==="Jan") {
            month = "01";
        } else if (month==="Feb") {
            month = "02";
        } else if (month==="Mar") {
            month = "03";
        } else if (month==="Apr") {
            month = "04";
        } else if (month==="May") {
            month = "05";
        } else if (month==="Jun") {
            month = "06";
        } else if (month==="Jul") {
            month = "07";
        } else if (month==="Aug") {
            month = "08";
        } else if (month==="Sep") {
            month = "09";
        } else if (month==="Oct") {
            month = "10";
        } else if (month==="Nov") {
            month = "11";
        } else if (month==="Dec") {
            month = "12";
        }

        for (let i = 0; i < ratings_global.length; i++) {
            if (ratings_global[i]['date'].slice(0,4)===year && 
            ratings_global[i]['date'].slice(5,7)===month && 
            ratings_global[i]['date'].slice(8,10)===day) {
                console.log('yes');
                return `${ratings_global[i]['rating']} - ${ratings_global[i]['description']}`;
            }
        }
        return "No Rating!"
    };

    return (
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
            <LocalizationProvider dateAdapter={AdapterDayjs}>
                <StaticDatePicker 
                displayStaticWrapperAs="desktop"
                openTo="day"
                value={date}
                onChange={(newValue) => {
                    setDate(newValue['$d']);
                    setRating(getDayRating(newValue['$d'].toString().slice(8,10), newValue['$d'].toString().slice(4,7), newValue['$d'].toString().slice(11,15)));
                }}
                renderInput={(params) => <TextField {...params} fullWidth/>}
                />
            </LocalizationProvider>
            <Box sx={{ overflow: "hidden", textOverflow: "ellipsis", width: 800, wordWrap: 'break-word', ml: 10 }}>
                <Typography variant='h3'>{rating}</Typography>
            </Box>
        </Box>
    );
}

const mapStateToProps = state => ({
    ratings_global: state.Profile.other_ratings,
});

export default connect(mapStateToProps)(RatingCalendar);