import React, { useEffect, useState } from 'react';
import { Box, Link, Typography, List, ListItem, ListItemButton, ListItemText, Divider,
        Grid, Card, Button, Container } from '@mui/material';
import { connect } from 'react-redux';
import { getFollowingRatings } from '../Actions/Profile';
import { useNavigate } from 'react-router-dom';
import RatingDisplay from '../Components/RatingDisplay'
import { createTheme, ThemeProvider } from '@mui/material/styles';

function Home({ ratings_global, getFollowingRatings, username, ratings_local }) {
    const navigate = useNavigate();
    const [haveRated, setHaveRated] = useState(false);
    const [cardElevation, setCardElevation] = useState(3);
    const [targetCard, setTargetCard] = useState(-1);

    useEffect(() => {
        getTodaysRating();
    });

    useEffect(() => {
        getFollowingRatings();
    }, []);

    const averageDay = (ratings) => {
        let sum = 0;
        ratings.forEach(item => {
            sum += item['rating'];
        });
        let avg = sum / ratings.length;
        return avg.toFixed(1);
    };

    const getTodaysRating = () => {
        let currentDate = new Date().toJSON().slice(0, 10);
        if (ratings_local.length > 0 && !haveRated) {
            if (ratings_local[ratings_local.length-1].date===currentDate) {
                setHaveRated(true);
            }
        }
    };

    const theme = createTheme({
        typography: {
            fontFamily: '"Sono", "Helvetica", "Arial", sans-serif',
            h1: {
                fontSize: 150,
            },
        },
        palette: {
            mode: 'dark',
            primary: {
              main: '#ff5349',
            },
            secondary: {
              main: '#800020',
            },
          },
      });

    return (
        <Grid container spacing={2} sx={{ px: 8 }}>
            <Grid item align='center' xs={12} sx={{ pb: 2 }}>
                <Typography variant='h3'>Today's Ratings</Typography>
            </Grid>
            <Grid item align='center' xs={6} sx={{ p: 4 }}>
                {ratings_global.length>0 ?
                    <ThemeProvider theme={theme}>
                        <Card elevation={3} sx={{ height: 300, display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
                            <Typography variant='body' component='p'>Average:</Typography>  
                            <RatingDisplay rating={averageDay(ratings_global)} />
                            <Typography variant='body' component='p'>Out of ten</Typography>
                        </Card>
                    </ThemeProvider>
                    :
                    <Card elevation={3} sx={{ height: 300, display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
                        <Typography variant='subtitle1'>No Ratings Yet!</Typography>  
                    </Card> 
                }
            </Grid>
            <Grid item align='center' xs={6} sx={{ p: 4 }}>
                {haveRated ?
                    <Link href={`/rating/${ratings_local[ratings_local.length-1].id}`} underline='none'>
                        <ThemeProvider theme={theme}>
                            <Card elevation={targetCard===ratings_local[ratings_local.length-1].id ? cardElevation : 3} onMouseOver={() => {
                                setCardElevation(10);
                                setTargetCard(ratings_local[ratings_local.length-1].id);
                            }} 
                            onMouseOut={() => setCardElevation(3)} 
                            sx={{ height: 300, display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
                                <Typography variant='subtitle1'>{username}'s Rating</Typography>
                                <RatingDisplay rating={ratings_local[ratings_local.length-1].rating} />
                                <Box>
                                    <Typography display='inline' variant='subtitle2'>{ratings_local[ratings_local.length-1].likes.length} Likes</Typography>
                                    <Typography display='inline' variant='subtitle2'> {ratings_local[ratings_local.length-1].comments.length} Comments</Typography>
                                </Box>
                            </Card>
                        </ThemeProvider>
                        
                    </Link>
                :
                    <Card elevation={3} align='center' sx={{ height: 300, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                        <Button variant='contained' color='primary' onClick={() => navigate('/rate')} sx={{ width: 200 }}>Rate Today</Button>
                    </Card>
                }
            </Grid>
            <Grid item align='center' xs={12} sx={{ pr: 4, pl: 7 }}>
                {ratings_global.length>0 ?
                    ratings_global.map((rating) => 
                        rating.user.username!=username ?
                            <Link href={`/rating/${rating.id}`} underline='none'>
                                <Card elevation={targetCard===rating.id ? cardElevation : 3} onMouseOver={() => {
                                        setCardElevation(10);
                                        setTargetCard(rating.id);
                                    }}
                                    onMouseOut={() => setCardElevation(3)} 
                                    sx={{ height: 300, mb: 2, display: 'flex', justifyContent: 'space-around', alignItems: 'center' }}>
                                    <Grid container>
                                        <Grid item xs={6} align='center'>
                                            <ThemeProvider theme={theme}>
                                                <RatingDisplay rating={rating.rating} />
                                            </ThemeProvider>
                                        </Grid>
                                        <Grid item xs={6} sx={{ mt: 5.5 }}>
                                            <Box sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', alignItems: 'flex-start' }}>
                                                <Typography variant='h3'>{rating.user.username}</Typography>
                                                <Box>
                                                    <Typography display='inline' variant='subtitle1'>{rating.likes.length} Likes</Typography>
                                                    <Typography display='inline' variant='subtitle1'> {rating.comments.length} Comments</Typography>
                                                </Box>
                                            </Box>
                                        </Grid>
                                    </Grid>
                                </Card>
                            </Link>
                        :
                            <div></div>
                    )
                    :
                    <Typography variant='subtitle1'>It's a little quiet...</Typography>
                }
            </Grid>
        </Grid>
    );
}

const mapStateToProps = state => ({
    username: state.Profile.username,
    ratings_global: state.Profile.other_ratings,
    ratings_local: state.Profile.ratings
});

export default connect(mapStateToProps, { getFollowingRatings })(Home);