import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { rate } from '../Actions/Profile';
import { useNavigate } from 'react-router-dom';
import { Container, Card, Typography, TextField, Button, Slider, Box } from '@mui/material';
import { loadUser } from '../Actions/Profile';

function RateDay({ rate, ratings, loadUser }) {
    const navigate = useNavigate();
    const [formData, setFormData] = useState({
        rating: 0,
        description: ''
    });
    const { rating, description } = formData;

    useEffect(() => {
        loadUser();
        navigateToRating();
    });

    const navigateToRating = () => {
        let currentDate = new Date().toJSON().slice(0, 10);
        for (let i = 0; i < ratings.length; i+=1) {
            if (ratings[i].date===currentDate) {
                navigate(`/rating/${ratings[i].id}`);
            }
        }
    };

    const valueText = (value) => {
        return `${value}`;
    };

    const onChange = e => {
        setFormData({ ...formData, [e.target.id]: e.target.value });
    };

    const handleSliderChange = e => {
        setFormData({ ...formData, rating: e.target.value })
    };

    const onSubmit = e => {
        e.preventDefault();

        if (rating <= 10 && rating >= 0 && description.length <= 200 && description.length >= 0) {
            rate(rating, description);
        }
    };

    return (
        <Container maxWidth="md" sx={{ display: 'flex', justifyContent: 'center' }}>
            <Card elevation={3} sx={{ mt: 8, p: 20 }} align='center'>
                <Typography variant='h4'>Rate Today!</Typography>
                <Box sx={{ my: 2}}>
                    <Typography variant='h4'>{rating}</Typography>
                </Box>
                <form onSubmit={e => onSubmit(e)}>
                    <Slider
                        id='rating'
                        defaultValue={0}
                        getAriaValueText={valueText}
                        valueLabelDisplay="auto"
                        step={1}
                        marks
                        min={0}
                        max={10}
                        onChange={e => handleSliderChange(e)}
                    />
                    <TextField 
                        id="description"
                        label="Message of the Day"
                        multiline
                        fullWidth
                        rows={8}
                        onChange={e => onChange(e)}
                    />
                    <Button fullWidth 
                        type="submit" 
                        color="primary" 
                        variant="contained" 
                        sx={{ mt: 6 }}>
                        Rate!
                    </Button>
                </form>
            </Card>
        </Container>
    );
}

const mapStateToProps = state => ({
    ratings: state.Profile.ratings
});

export default connect(mapStateToProps, { rate, loadUser })(RateDay);