import { connect } from 'react-redux';
import { logout } from '../Actions/Auth';
import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import SearchUsers from './SearchUsers';
import { AppBar, Box, Toolbar, Typography, IconButton, MenuItem, Menu, Button, InputBase, Avatar } from '@mui/material';

function Navbar({ isAuthenticated, logout, username }) {
    const navigate = useNavigate();
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleAccountClick = () => {
        navigate('/my-account');
        handleClose();
    };

    const handleProfileClick = () => {
        navigate(`/profile/${username}`);
        handleClose();
    };

    const handleNotAuthenticated = () => {
        navigate('/login');
        handleClose();
    };

    const Search = styled('div')(({ theme }) => ({
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: alpha(theme.palette.common.white, 0.15),
        '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
        },
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(1),
        width: 'auto',
        },
    }));
    
    const SearchIconWrapper = styled('div')(({ theme }) => ({
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    }));
    
    const StyledInputBase = styled(InputBase)(({ theme }) => ({
        color: 'inherit',
        '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: '12ch',
            '&:focus': {
            width: '20ch',
            },
        },
        },
    }));

    return (
        <AppBar position="sticky" sx={{ background: 'transparent', boxShadow: 'none' }}>
            <Toolbar>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                    <Button 
                        key={"Home page"}
                        sx={{ my: 2, color: 'white', display: 'block' }}
                        onClick={() => navigate('/')}
                    >
                        <Typography textAlign="center" color='primary'>Day Rater</Typography>
                    </Button>
                </Typography>
                <SearchUsers />
                <Box>
                    <IconButton
                        size="large"
                        aria-label="account of current user"
                        aria-controls="menu-appbar"
                        aria-haspopup="true"
                        onClick={handleMenu}
                        color="inherit"
                    >
                        <Avatar color='primary'>{username.charAt(0)}</Avatar>
                    </IconButton>
                    <Menu
                        id="menu-appbar"
                        anchorEl={anchorEl}
                        anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                        }}
                        keepMounted
                        transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                        }}
                        open={Boolean(anchorEl)}
                        onClose={handleClose}
                    >
                        {isAuthenticated ? 
                        <div>
                            <MenuItem onClick={handleProfileClick}>Profile</MenuItem>
                            <MenuItem onClick={handleAccountClick}>My account</MenuItem>
                            <MenuItem onClick={() => {
                            logout();
                            navigate('login');
                            handleClose();
                        }}>Logout</MenuItem>
                        </div>
                        : <MenuItem onClick={handleNotAuthenticated}>Sign In</MenuItem>}
                    </Menu>
                </Box>
            </Toolbar>
        </AppBar>
    );
}

const mapStateToProps = state => ({
    username: state.Profile.username,
    isAuthenticated: state.Auth.isAuthenticated
});

export default connect(mapStateToProps, { logout })(Navbar);