import React from 'react';
import { BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import Home from './Containers/Home';
import Register from './Containers/Register';
import Dashboard from './Containers/Dashboard';
import Login from './Containers/Login';
import Layout from './HOCS/Layout';
import Profile from './Containers/Profile';
import Followage from './Containers/Followage';
import RateDay from './Containers/RateDay';
import Rating from './Containers/Rating';

import { Provider } from 'react-redux';
import store from './Store';

function App(props) {
    return (
        <Provider store={store}>
            <Router>
                <Layout>
                    <Routes>
                        <Route exact path='/' element={<Home/>}></Route>
                        <Route path='/register' element={<Register/>}></Route>
                        <Route path='/login' element={<Login/>}></Route>
                        <Route path='/my-account' element={<Dashboard/>}></Route>
                        <Route path='/profile/:username' element={<Profile />}></Route>
                        <Route path='/following/:username' element={<Followage mode='Following' />}></Route>
                        <Route path='/followers/:username' element={<Followage mode='Followers' />}></Route>
                        <Route path='/rate' element={<RateDay />}></Route>
                        <Route path='/rating/:id' element={<Rating />}></Route>
                    </Routes>
                </Layout>
            </Router>
        </Provider>
    );
}

export default App;