import axios from 'axios';
import Cookies from 'js-cookie';
import { loadUser } from './Profile';
import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL,
    AUTHENTICATED_SUCCESS,
    AUTHENTICATED_FAIL,
    
} from './Types';

export const checkAuthenticated = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    };

    const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/authenticated`, config)
    try {
        if (response.data.Success) {
            dispatch({
                type: AUTHENTICATED_SUCCESS,
                payload: response.data.isAuthenticated
            });
        } else {
            dispatch({
                type: AUTHENTICATED_FAIL,
                payload: response.data.isAuthenticated
            });
        }
    } catch {
        dispatch({
            type: AUTHENTICATED_FAIL,
            payload: false
        });
    }
}

export const login = (username, password) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({ username, password });

    const response = await axios.post(`${process.env.REACT_APP_API_URL}/api/login`, body, config)
    try {
        if (response.data.Error) {
            dispatch({
                type: LOGIN_FAIL
            });
        } else {
            dispatch({
                type: LOGIN_SUCCESS,
            });
            dispatch(loadUser());
        }
    } catch {
        if (response.data.Error) {
            dispatch({
                type: LOGIN_FAIL
            });
        }
    }
}

export const logout = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({
        'withCredentials': true
    });

    const response = await axios.post(`${process.env.REACT_APP_API_URL}/api/logout`, body, config)
    try {
        if (response.data.Error) {
            dispatch({
                type: LOGOUT_FAIL
            });
        } else {
            dispatch({
                type: LOGOUT_SUCCESS,
            });
        }
    } catch {
        if (response.data.Error) {
            dispatch({
                type: LOGOUT_FAIL
            });
        }
    }
}

export const register = (username, password, re_password) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }

    const body = JSON.stringify({ username, password, re_password });

    const response = await axios.post(`${process.env.REACT_APP_API_URL}/api/register`, body, config);
    try {
        if (response.data.Error) {
            dispatch({
                type: REGISTER_FAIL
            });
        } else {
            dispatch({
                type: REGISTER_SUCCESS
            });
        }
    } catch {
        if (response.data.Error) {
            dispatch({
                type: REGISTER_FAIL
            });
        }
    }
}