import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { loadOtherUser } from '../Actions/Profile';
import { Typography, Box, Divider, Avatar } from '@mui/material'
import AccountCircle from '@mui/icons-material/AccountCircle';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';

function Followage({
    loadOtherUser,
    followers_global,
    following_global,
    mode
}) {
    const navigate = useNavigate();
    const {username} = useParams();

    useEffect(() => {
        loadOtherUser(username);
    }, [])

    const handleProfileClick = (username) => {
        navigate(`/profile/${username}`);
    };
    
    return (
        <Box sx={{ m: 4 }}>
            <Box sx={{ mb: 2 }}>
                <Typography variant='h3' color='primary'>{mode}</Typography>
            </Box>
            <Divider variant="middle" sx={{ color: 'black' }}/>
            <Box>
                <nav>
                    <List>
                        {/* something to do with 'map' */}
                        {mode==="Followers" ? 
                            followers_global.length>0 ? followers_global.map((followageProfile) => 
                                <Box>
                                    <ListItem disablePadding>
                                        <ListItemButton onClick={() => handleProfileClick(followageProfile['username'])}>
                                            <ListItemIcon>
                                                <Avatar>{followageProfile['username'].charAt(0)}</Avatar>
                                            </ListItemIcon>
                                            <ListItemText primary={followageProfile['username']} />
                                        </ListItemButton>
                                    </ListItem>
                                    <Divider />
                                </Box>
                                )
                                :
                                <ListItem disablePadding>
                                    <ListItemText primary="It's a little quiet around here..." />
                                </ListItem>                            
                        :
                            following_global.length>0 ? following_global.map((followageProfile) => 
                                <Box>
                                    <ListItem disablePadding>
                                        <ListItemButton onClick={() => handleProfileClick(followageProfile['username'])}>
                                            <ListItemIcon>
                                                <Avatar>{followageProfile['username'].charAt(0)}</Avatar>
                                            </ListItemIcon>
                                            <ListItemText primary={followageProfile['username']} />
                                        </ListItemButton>
                                    </ListItem>
                                    <Divider />
                                </Box>
                                
                                )
                                :
                                <ListItem disablePadding>
                                    <ListItemText primary="It's a little quiet around here..." />
                                </ListItem>
                        }
                    </List>
                </nav>
            </Box>
        </Box>
    );
}

const mapStateToProps = state => ({
    first_name_global: state.Profile.other_first_name,
    last_name_global: state.Profile.other_last_name,
    bio_global: state.Profile.other_bio,
    followers_global: state.Profile.other_followers,
    following_global: state.Profile.other_following,
    ratings_global: state.Profile.other_ratings,
});

export default connect(mapStateToProps, { loadOtherUser })(Followage);