import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { updateProfile } from '../Actions/Profile';
import { Container, Card, Typography, TextField, Button } from '@mui/material';
import CSRFToken from '../Components/CSRFToken';

function Dashboard({
    username,
    updateProfile,
    isAuthenticated
}) {
    const navigate = useNavigate();
    const [formData, setFormData] = useState({
        first_name: '',
        last_name: '',
        bio: '',
    });
    const { first_name, last_name, bio } = formData;
    const [formSubmitted, setFormSubmitted] = useState(false);

    const onChange = e => setFormData({ ...formData, [e.target.id]: e.target.value })

    const onSubmit = e => {
        e.preventDefault();

        updateProfile(first_name, last_name, bio);
        setFormSubmitted(!formSubmitted);
        navigate(`/profile/${username}`);
    }

    if (!isAuthenticated) {
        navigate('/login');
    }

    return (
        <Container maxWidth="md" sx={{ display: 'flex', justifyContent: 'center'}}>
            <Card elevation={3} sx={{ mt: 8, p: 12, width: 700 }} align='center'>
                <Typography variant='h4'>Update Profile</Typography>
                <form onSubmit={e => onSubmit(e)}>
                    <CSRFToken />
                    <TextField
                        fullWidth
                        id="first_name"
                        label="First Name"
                        onChange={e => onChange(e)}
                        sx={{
                            mt: 5,
                        }}
                    />
                    <TextField
                        fullWidth
                        id="last_name"
                        label="Last Name"
                        onChange={e => onChange(e)}
                        sx={{ my: 6 }}
                    />
                    <TextField 
                        id="bio"
                        label="Bio"
                        multiline
                        fullWidth
                        rows={8}
                        onChange={e => onChange(e)}
                    />
                    <Button fullWidth 
                        type="submit" 
                        color="primary" 
                        variant="contained"
                        sx={{ mt: 6 }}>
                        Update Profile
                    </Button>
                </form>
            </Card>
        </Container>
    );
}

const mapStateToProps = state => ({
    username: state.Profile.username,
    isAuthenticated: state.Auth.isAuthenticated
});

export default connect(mapStateToProps, { updateProfile })(Dashboard);